@use "global" as *;

.errorpage__main {
    margin-top: rem(50);
}

.errorpage {
    padding-top: rem(80);
    padding-bottom: rem(80);

    @include mq(md) {
        padding-top: rem(143);
    }
}

.errorpage__inner {
    text-align: center;

}

.errorpage__404message {
    display: block;
    font-size: rem(40);
    letter-spacing: 0.05em;
    color: #333;
    line-height: 0.875;
    opacity: 0.3;
    margin-bottom: rem(40);


    @include mq(md) {
        font-size: rem(60);
        letter-spacing: 0.03333em;
    }
}

.errorpage__text {
    display: block;
    font-size: rem(20);
    letter-spacing: 0.1em;
    margin-top: rem(80);

    @include mq(md) {
        font-size: rem(30);
        letter-spacing: 0.06667em;
        margin-top: rem(65);
    }
}



.errorpage__message span:not(:first-child) {
    margin-top: rem(80);

    @include mq(md) {
        margin-top: rem(65);
    }
}

.sp__show {
    display: block;

    @include mq(md) {
        display: none;
    }
}