@use "global" as *;

.header {
    top: 0;
    left: 0;
    right: 0;
    height: rem(90);
    z-index: 99999;
    transition: background-color 0.4s ease-out;
    background-color: $white;

}

.header__inner {
    display: flex;
    height: inherit;
    justify-content: space-between;
    padding: rem(16) rem(10);

    @include mq(md) {
        padding-left: rem(34);
        padding-right: 0;
    }
}

.header__logo {
    width: rem(75);

    @include mq(md) {
        width: rem(94);
        padding-top: rem(10);
    }
}
.header__spright  {
    display: flex;
    align-items: center;
    gap: rem(8);
    @include mq(md) {
        display: none;
    }

}

.header__sptel img {
    width: 30px;
}

.header__pcright {
    display: none;

    @include mq(md) {
        display: flex;
        gap: rem(30);
    }
}

.header__tel {
    display: flex;
    justify-content: end;
    font-size: rem(24);

    @include mq(md) {
        padding-right: rem(20);
    }
}

.header__tel img {
    width: rem(30);
    height: rem(36);
}

.header__sp-nav {
    display: none;
    position: absolute;
    background-color: #F00C27;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100vh;
    z-index: 8888;
}


.header__pc-nav {
    height: inherit;
    display: none;

    @include mq(md) {
        display: block;
    }
}
