@use "global" as *;

.index-news {
    margin-top: rem(54);
    background-color: #FFFFFF;
    border-radius: rem(27);
    position: relative;
    margin-inline: auto;
    padding: 0 rem(10);

    @include mq(md) {
        margin-top: rem(17);
        margin-left: 8%;
        max-width: rem(654);

    }   
}

.index-news__inner {
    padding: rem(6) rem(20);

    @include mq(md) {
        max-width: rem(1084);
        margin: auto;
        justify-content: space-between;
    }
}

.btn--news {
    position: absolute;
    top: 80%;
    right: 0;
    color: #fff;
    z-index: 1111;

    @include mq(md) {
        right: -10%;

    }
}

.btn--news a{
    padding: rem(20) rem(24); 
}
