@use "global" as *;

.access__map {
    width: 100vw;
    margin-left: calc(50% - 50vw);
    position: relative;
    margin-top: rem(60);
    padding-top: 100%;


    @include mq(md) {
        padding-top: 30%;
        margin-inline: auto;
        margin-top: rem(128);
        height: 100%;
        width: 100%;
    }

    iframe {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 100%;
        object-fit: cover;

    }
}

.access__text {
    text-align: center;
    color: #222;
    margin-top: rem(32);
    font-size: rem(18);
    margin-bottom: rem(50);

    @include mq(md) {
        font-size: rem(24);
        margin-top: rem(45);
        margin-bottom: rem(124);
    }
}