@use "global" as *;

.index-business {
    margin-top: rem(74);
    position: relative;
    z-index: 2;

    @include mq(md) {
        margin-top: rem(240);
    }
}

.index-business__bg {
    top: 12%;
    right: 0;
    @include mq(md) {
        top: 18%;
        right: 0;
    }
}

.index-business__cube {
    margin-top: rem(-20);
    margin-left: auto;

    @include mq(md) {
        margin-top: rem(-210);
    }
}

.index-business__inner {
    margin: auto;
}


.index-business__wrap {
    display: flex;
    flex-direction: column;
    margin-top: rem(76);

        @include mq(md) {
            justify-content: center;
            align-items: flex-start;
            flex-direction: row;
            margin-top: rem(186);
    }
}

.index-business__img0 {
    aspect-ratio: 440 / 330;
    max-width: rem(335);
    overflow: hidden;
    margin: auto;

    @include mq(md) {
        max-width: rem(440);
        margin-top: rem(76);
        margin-inline: initial;
    }
}

.index-business__img0 img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.index-business__box {
    @include mq(md) {
        margin-top: 0;
    }
}

.index-business__contents {
    margin-top: rem(106);

    @include mq(md) {
        margin-top: rem(40);
        display: flex;
    }
}

.index-business__content {
    color: #333;
    box-shadow: 5px 5px 6px #00000029;
    margin-top: rem(60);
    display: flex;
    flex-direction:column;
    justify-content: space-between;
    padding: rem(20) rem(20) rem(40) rem(20);

    
    @include mq(md) {
        margin-top: 0;
        max-width: rem(260);
        margin-left: rem(16);
        padding: rem(60) rem(20) rem(30) rem(20);

    }
}

.index-business__img {
    max-width: rem(220);
    aspect-ratio: 200 / 150;
    overflow: hidden;
    margin: auto;
    
    @include mq(md) {
        max-width: rem(200);
    }
}

.index-business__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.index-business__subtitle {
    text-align: center;
    font-size: rem(28);


    @include mq(md) {
        font-size: rem(32);
        margin-top: rem(40);

    }
}

.index-business__text {
    line-height: 1.5;
    margin-top: rem(20);
    font-size: rem(14);

    @include mq(md) {
        max-width: rem(238);
        font-size: rem(16);
        margin-top: rem(32);
        height: rem(280);
    }
}

.index-business__btn {
    text-align: center;
    margin-top: rem(26);

    @include mq(md) {
        margin-top: 0;
    }
}