@use "global" as *;
.company{
    margin-top: rem(0);
    color: #333;

    @include mq(md) {
        margin-top: rem(34);
    }
}

.company__inner {
    padding: 0 rem(20);
}

.company__message {
    margin-top: rem(55);
    margin-bottom: rem(77);

    @include mq(md) {
        display: flex;
        flex-direction: row-reverse;
        align-items:flex-start;
        justify-content: center;
        gap: rem(40);
        margin-top: rem(148);
        margin-bottom: 0;
    }
}

.company__ceo {
    text-align: center;

    @include mq(md) {
        margin-top: rem(40);
    }
}

.company__ceo img{
    aspect-ratio: 331/406;
    max-width: rem(268);
    margin-inline: auto;


    @include mq(md) {
        max-width: rem(330);
        margin-inline: initial;
    }

}

.company__position{
    font-size: rem(16);
    margin-top: rem(16);
    letter-spacing: 0.08em;

}

.company__name{
    font-size: rem(20);
    font-weight: bold;
    letter-spacing: 0.1em;

    
}

.company__name--en{
    font-size: rem(20);
    font-weight: bold;
    letter-spacing: 0.1em;
    
}

.company__profile{
    margin-top: rem(40);
    box-shadow: 5px 5px 6px #00000029;
    padding-top: rem(30);
    padding-bottom: rem(24);

    @include mq(md) {
        margin: 0;
    }

}

.company__box {
    padding: rem(12);
    margin: auto;

}

.company__title--sub {
    font-size: rem(22);
    position: relative;
    display: block;
    margin-inline: auto;
    font-weight: bold;

}

.company__title--sub::before {
    position: absolute;
    content: "";
    border: rem(2) solid #F00C27;
    width: rem(58);
    bottom: -40%;
    left: 50%;
    transform: translateX(-50%);
}

.company__ceoprofile {
    margin-top: rem(26);
    display: flex;
    flex-direction: column;
}


.company__profiledetail {
    font-size: rem(14);
    margin-inline: auto;
    margin-top: rem(40);
    letter-spacing: 0.09em;
}

.company__profiledetail p {
    margin-top: rem(4);
}


.company__lists {
    color: #222;
    box-shadow: 5px 5px 6px #00000029;
    margin-top: rem(20);

    @include mq(md) {
        margin-inline: auto;
        max-width: rem(1105);
        margin-top: rem(125);

    }
}

.company__list {
    display: flex;
    border-bottom: 1px solid #CECECE;
    font-size: rem(14);
    padding: rem(30) rem(10);
    width: 90%;
    align-items: center;

    @include mq(md) {
        font-size: rem(24);
        padding-top: rem(38);
        padding-bottom: rem(45);
        padding-right: rem(57);
        width: 90%;
    }

    dt {
        line-height: 1.61538;
        width: 33%;
        padding-left: rem(5);
        font-weight: bold;
        text-align: center;


        @include mq(md) {
            line-height: 2;
            width: 25%;
        }
    }

    dd {
        line-height: 1.84615;
        width: 67%;
        padding-left: rem(4);

        @include mq(md) {
            line-height: 2;
            letter-spacing: 0.05em;
            padding-left: rem(14);
            width: 75%;
        }   
    }
}

