@use "global" as *;

.btn {
    display: inline-block;
    font-size: rem(16);
    letter-spacing: 0.1em;
    background-color: #FC0F11;
    border-radius: rem(17);
}

.btn a {
    display: block;
    padding: rem(18) rem(70);
    color:$white;
}


.btn--business a{
    @include mq(md) {
        padding: rem(20) rem(40);
    }
}