@use "global" as *;

.square-bg {
    width: 80%;
    height: rem(400);
    background:#AF261B3C;
    position: absolute;
    clip-path: polygon(0 50%, 100% 0, 100% 54%, 14% 97%);
    z-index: -1;
    right: 0;

    @include mq(md) {
        width: 100%;
        clip-path: polygon(0 70%, 100% 0%, 100% 35%, 10% 100%);
        max-width: rem(972);
        height: rem(662);
    }
}
