@use "global" as *;

.mainview {
    height: rem(240);
    background-color: gray;
    padding-top: rem(60);


    @include mq(md) {
        padding-top: rem(160);
        height: rem(434);
    }
}

.mainview__title {
    position: relative;
    padding-left: rem(60);

    @include mq(md) {
        padding-left: rem(90);
    }
}

.mainview__title::before {
    position: absolute;
    content: "";
    border: rem(2) solid #F00C27;
    height: rem(96);
    left: 7%;

    @include mq(md) {
        height: rem(140);
        left: 4%;
    }
}

.mainview__title--ja {
    font-size: rem(38);

    @include mq(md) {
        font-size: rem(60);
    }
}

.mainview__title--en {
    font-size: rem(20);
    font-weight: bold;

    @include mq(md) {
        font-size: rem(32);
    }
}