@use "global" as *;

.steps {
    margin-top: rem(40);
}

.step {
    display: flex;
    align-items: center;
    padding: rem(10) rem(20);
    box-shadow: 5px 5px 6px #00000029;
    margin-inline: auto;
    gap: rem(10);

    @include mq(md) {
        padding: rem(10) rem(40);
        max-width: rem(766);
        gap: 0;

    }
}

.step__left {
    color: #F00C27;
    text-align: center;
    font-weight: bold;
    font-size: rem(18);

    @include mq(md) {
        font-size: rem(22);
    }
}

.step__right {
    font-size: rem(14);
    margin-inline: auto;

    @include mq(md) {
        font-size: rem(16);
        padding-right: rem(40);
    }

}

.step__arrow {
    width: rem(23);
    height: rem(18);
    background-color: #F00C27;
    clip-path: polygon(50% 100%, 0 0, 100% 0);
    margin-top: rem(30);
    margin-bottom: rem(24);
    margin-inline: auto;

}