@use "global" as *;

.title {
    position: relative;

    @include mq(md) {
        text-align: start;
        margin: auto;
        margin-left: 20px;
        margin-top: -10%;
    }
}

.title::before {
    position: absolute;
    content: "";
    border: rem(3) solid #F00C27;
    height: rem(66);
    margin-top: rem(4);

    @include mq(md) {
        height: rem(86);
        margin-top: rem(16);
    }
}

.title__english {
    font-size: rem(34);
    letter-spacing: 0.04em;
    color: #333333;
    font-weight: bold;
    margin-left: rem(20);
    margin-top: rem(-50);


    @include mq(md) {
        font-size: rem(44);
        margin-left: rem(20);
        margin-top: 0;
    }
}

.title__english--bg {
    font-size: rem(60);
    color: #E6E6E69D;
    font-weight: bold;
    padding-left: rem(24);

    @include mq(md) {
        font-size: rem(210);
        margin-top: -18%;
        padding-left: rem(40);
    }
}

.title__english--bg--margin {
    @include mq(md) {
        margin-top: 0;
    }
}

.title__japanese {
    font-size: rem(14);
    letter-spacing: 0.1em;
    color: #333333;
    margin-left: rem(20);


    @include mq(md) {
        font-size: rem(16);
        margin-top: rem(8);
        margin-left: rem(20);
    }
}

.title__underline {
    position: relative;
    text-align: center;
}

.title__underline::before {
    position: absolute;
    content: "";
    border: rem(4) solid #F00C27;
    width: rem(86);
    bottom: -22%;
    left: 50%;
    transform: translateX(-50%);
}

.title__underline--training::before {
    width: rem(86);
    bottom: -80%;

    @include mq(md) {
        width: rem(120);
    }
}


.title__underline--en {
    font-weight: bold;
    font-size: rem(34);

    @include mq(md) {
        font-size: rem(44);
        margin-top: rem(6);
    }
}

.title__underline--ja {
    font-size: rem(14);

    @include mq(md) {
        margin-top: rem(6);
    }
}