@use "global" as *;

.sp-nav__items {
    margin-left: rem(2);
    padding-top: rem(192);
    padding-bottom: rem(208);
    height: 100vh;
    overflow: scroll;
}

.sp-nav__item a {
    display: block;
    width: 100%;
    padding-top: rem(16);
    padding-bottom: rem(15);
    text-align: center;
    color: #fff;
    font-size: rem(16);
    letter-spacing: 0.125em;
}

.sp-nav__item--contact {
    margin: auto;
    z-index: 9999;
    margin-top: rem(56);
}

.sp-nav__item--contact a {
    display: flex;
    justify-content: center;
    border: 1px solid #FFFFFF;
    width: rem(190);
    margin: auto;
    border-radius: 10px;
    text-align: center;
}

.sp-nav__item--contact a img {
    display: block;
    width: rem(28);
    height: rem(28);
}

.sp-nav__item--contact a span {
    display: block;
    margin-left: rem(10);
}