@use "global" as *;

.top-mainview {
    width: 100%;
    margin-top: rem(-30);
}

.top-mainview__wrap {
  margin-top: rem(36);

  @include mq(md) {
    margin-top: rem(64);
    display: flex;
  }
}

.top-mainview__inner {
    background: #E6E6E69D;
    padding-bottom: rem(90);

    @include mq(md) {
      width:93%;
      height: rem(580);
      padding-top: rem(34);
    }
}

.top-mainview__title ,.top-mainview__subtitle {
    font-weight: bold;
    line-height: 1;
    letter-spacing: 0.01786em;
}

.top-mainview__title {
    color: $white;
    font-size: rem(24);
    padding-left: rem(30);

    @include mq(md) {
      font-size: rem(70);
      letter-spacing: 0.08em;
      margin-left: 6%;
    }
}

.top-mainview__title span {
  display: inline-block;
  background: linear-gradient(180deg, #FF001E 0%, #FF0422 53%, #FF919F 100%);
  margin-top: rem(22);
  padding: rem(20) rem(10);

}

.top-mainview__subtitle {
    color: #F00A26;
    font-size: rem(18);
    margin-top: rem(45);
    letter-spacing: 0.025em;
    display: flex;
    flex-direction: column;
    gap: rem(10);
    padding-left: rem(30);

    @include mq(md) {
      font-size: rem(18);
      letter-spacing: 0.02em;
      margin-top: rem(24);
      margin-left: rem(470);
    }
}

.top-mainview__sub1 {
  display: flex;
  gap: rem(10);
}

.top-mainview__sub1 p {
  padding: rem(12) rem(44);
    @include mq(md) {
      padding: rem(12) rem(43);
    }
}

.top-mainview__sub2 p {
  padding: rem(12) rem(14);
}

.top-mainview__subtitle p {
  display: inline-block;
  background: #fff;
  line-height: 1.5;
}
 
  
.top-mainview__img {
  margin: 0 auto;
  height: 100vh;
  width: 100%;
}

.top-mainview__img img {
  height: 100vh;
  width: 100%;
  object-fit: cover;
}

.top-mainview__side {
  display: none;

  @include mq(md) {
    writing-mode: vertical-rl;
    text-orientation: upright;
    display: inline-block;
    margin-top: 34px;
    letter-spacing: 0.09em;
    margin-inline: auto;
    padding-left: rem(20);
    font-weight: bold;
    font-size: rem(14);
  }
}