@use "global" as *;

.pc-nav__items {
    display: flex;
    align-items: center;
    height: inherit;
}

.pc-nav__item {
    height: inherit;
}

.pc-nav__item a {
    color: #333333;
    font-size: rem(22);
    height: rem(54);
    display: grid;
    place-items: center;
    padding: 0 rem(15);
    letter-spacing: 0.125em;
    font-weight: 500;
}


.pc-nav__item a span {
    color: #333333;
    font-size: rem(12);
}

.pc-nav__contact {
    background-color: #F00C27;
    display: flex;
    align-items: center;    
    text-align: center;
    padding: rem(30) rem(30);
    height: rem(124);
    margin-top: rem(-16);
    display: none;

    @include mq(md) {
        display: block;
    }
}

.pc-nav__contact a span {
    color: $white;
}

.pc-nav__contact a img {
    width: rem(28);
    height: rem(28);
    margin: auto;

}