@use "global" as *;

.index-about {
    margin-top: rem(52);
    position: relative;
    z-index: 2;

    @include mq(md) {
        margin-top: rem(80);
    }
}

.index-about__bg {
    top: 40%;
    @include mq(md) {
        top: 26%;
    }
}

.index-about__cube {
    margin-top: rem(-128);

    @include mq(md) {
        margin-top: rem(-128);
    }
}

.index-about__inner {
    margin: auto;
}


.index-about__wrap {
    display: flex;
    flex-direction: column-reverse;
    margin-top: rem(76);

    @include mq(md) {
        flex-direction: row;
        margin-top: 0;
    }
}

.index-about__img {
    aspect-ratio: 430 / 322;
    max-width: rem(293);
    overflow: hidden;
    margin: auto;
    @include mq(md) {
        aspect-ratio: 430 / 322;
        max-width: rem(430);
    }
}

.index-about__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.index-about__box {
    margin-top: rem(37);
    @include mq(md) {
        margin-top: rem(186);
    }
}


.index-about__btn {
    margin-top: rem(60);
    text-align: center;

    @include mq(md) {
        margin-top: rem(60);
    }
}