@use "global" as *;

.category {
    margin-top: rem(51);

    @include mq(md) {
        margin-top: rem(82);
    }
}

.category__items {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    text-transform: uppercase;
    letter-spacing: 0.125em;
    padding-left: rem(2);

    @include mq(md){
    }
}


.category__item {
    margin-top: rem(16);
}

.category__item:nth-child(2),.category__item:nth-child(3) {
    margin-left: rem(24);

    @include mq(md) {
        margin-left: rem(40);
    }
}

.category__item:first-child {
    margin-left: 0;
}

.category__item:nth-child(4) {
    margin-left: 0;

    @include mq(md) { 
        margin-left: rem(40);
    }

    @include mq(sm) { 
        margin-left: rem(24);
    }
}

.category__item:nth-child(5) {
    margin-left: rem(24);
    @include mq(md) { 
        margin-left: rem(40);
    }

    @include mq(sm) { 
        margin-left: rem(40);
    }

}

.category__item--white {
    background-color: $white;
    color: #333;
    padding: rem(3) rem(8);

    @include mq(md) {
        padding: rem(2) rem(9);
    }
}

.category__item .is-active {
    background-color: $white;
    color: #333;
    padding: rem(2) rem(7);

    @include mq(md) {
        padding: rem(2) rem(9);
    }
}
