@use "global" as *;

.training {
    margin-top: rem(0);
    color: #333;
    position: relative;
    z-index: 2;

    @include mq(md) {
        margin-top: rem(36);
    }
}

.training__bg {
    top: 13%;
    @include mq(md) {
        top: 17%;
    }
}


.training__inner {
    padding: 0 rem(20);
    margin: auto;

    @include mq(md) {
        padding-left: rem(10);
        padding-right: rem(10);
    }
}

.training__content:first-child {
    margin-top: 0;
}

.training__content {
    margin-top: rem(68);

    @include mq(md) {
        margin-top: rem(118);
    }
}

.training--box {
    margin-top: rem(34);
    margin-inline: auto;
    padding: rem(22);
    letter-spacing: 0.1em;

    @include mq(md) {
        margin-top: rem(40);
        margin-left: rem(140);
        max-width: rem(700);
        padding: rem(55) rem(55);  
    }
}

.training--box.box__text {
    margin-top: 0;
}


.training__cube {
    margin-top: rem(10);

    @include mq(md) {
        margin-top: rem(-40);
    }
}

.training__class {
    margin-top: rem(20);
    
    @include mq(md) {
        margin-top: rem(150);
    }
}

.training__lists {
    display: flex;
    margin-top: rem(80);
    gap: rem(30);
    justify-content: center;
    flex-wrap: wrap;

    @include mq(md) {
        gap: rem(38);
        flex-wrap: nowrap;
    }
}

.training__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 25%;

    @include mq(md) {
        font-size: rem(20);
    }
}

.training__list img{
    max-width: rem(66);

    @include mq(md) {
        max-width: rem(85);
    }
}

.training__list p {
    max-width: rem(110);
    font-size: rem(14);
    margin-top: rem(14);
    text-align: center;


    @include mq(md) {
        display: block;
        max-width: rem(123);
    }
}

.training__step {
    margin-top: rem(84);
    margin-inline: auto;

    @include mq(md) {
        margin-top: rem(130);
    }
}

.training__maintitle {
    margin-left: 30px;

    @include mq(md) {
        margin-left: rem(200);
    }
}

.training__tablebox {
    margin-top: rem(90);

    @include mq(md) {
        margin-top: rem(116);
    }
}

.training__attention {
    font-size: rem(14);
    margin-top: rem(24);
    margin-bottom: rem(64);
    line-height: 1.8;


    @include mq(md) {
        color: #333;
        margin-top: rem(27);
        margin-bottom: rem(56);
        max-width: rem(908);
        width: 70%;
        margin-inline: auto;
    }
}

.training__swiper {
    margin-top: rem(64);

    @include mq(md) {
        margin-top: rem(30);
    }
}