@use "global" as *;

$drawer-width: 22px; //ドロワーの幅
$drawer-height: 2px; //ドロワー線の高さ
$drawer-color: #fff; //ドロワー線の色

.header__drawer{
 z-index:9999;
 margin-top: rem(-6);
 background-color: #F00C27;
 height: rem(55);
 width: rem(60);
 text-align: center;
 border-radius: rem(4);


 @include mq(md) {
    display: none;
  }
}

.drawer-text {
  color: $white;
  font-size: rem(10);
}

.header__drawer.is-open .drawer-text {
  display: none;
}

.header__drawer span {
 position: relative;
 display: block;
 height: $drawer-height;
 width: $drawer-width;
 border-radius: rem(10);
 background: $drawer-color;
 transition: ease .3s;
 left: 50%;
 transform: translateX(-50%);
}

.header__drawer span:nth-child(1) {
 top: 0;
}

.header__drawer span:nth-child(2) {
 margin: rem(5) 0;
}

.header__drawer span:nth-child(3) {
 top: 0;
}
/*is-OPEN時の動き*/
.header__drawer.is-open span:nth-child(1) {
 top: rem(10);
 transform: rotate(45deg);
 left: 30%;

}

.header__drawer.is-open span:nth-child(2) {
 transform: translateY(-50%);
 opacity: 0;
 left: 30%;

}

.header__drawer.is-open span:nth-child(3) {
 top: rem(-4);
 transform: rotate(-45deg);
 left: 30%;

}

.drawer-text--close {
  display: none;
  color: $white;
  font-size: rem(10);
  margin-top: 3px;
}

.header__drawer.is-open .drawer-text--close {
  display: block;
}

