@use "global" as *;

.business {
    margin-top: rem(0);
    color: #333;
    position: relative;
    z-index: 2;

    @include mq(md) {
        margin-top: rem(36);
    }
}

.business__bg1 {
    top: 12%;

    @include mq(md) {
        top: 12%;
    }
}

.business__bg2 {
    top: 49%;

    @include mq(md) {
        top: 51%;
    }
}

.business__bg3 {
    top: 72%;

    @include mq(md) {
        top: 70%;
    }
}

.business__bg4 {
    top: 92%;

    @include mq(md) {
        top: 90%;
    }
}

.business__inner {
    padding: 0 rem(20);
    margin: auto;

    @include mq(md) {
        padding-left: rem(10);
        padding-right: rem(10);
    }
}

.business__cube1 {
    margin-top: rem(-10);

    @include mq(md) {
    }
}

.business__cube2 {
    margin-top: rem(-4);
    margin-left: auto;

    @include mq(md) {
    }
}

.business__cube3 {
    margin-top: rem(-4);


    @include mq(md) {
        margin-top: rem(-10);

    }
}

.business__cube4 {
    margin-top: rem(-46);
    margin-left: auto;
    margin-bottom: rem(66);

    @include mq(md) {
        margin-top: rem(-60);
        margin-bottom: rem(110);

    }
}


.business__content:first-child {
    margin-top: 0;
}

.business__content {
    margin-top: rem(60);

    @include mq(md) {
        margin-top: rem(0);
    }
}

.business__list {
    margin-top: rem(42);
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include mq(md) {
        margin-top: rem(90);
        flex-direction: row-reverse;
    }
}

.business__list--reverse {
    @include mq(md) {
        flex-direction: row;
    }
}

.business__img {
    aspect-ratio: 474 / 355;
    overflow: hidden;

    @include mq(md) {
        aspect-ratio: 460 / 270;
        max-width: rem(460);
        margin-left: rem(-30);
    }
}

.business__img--reverse {
    @include mq(md) {
        position: relative;
        z-index: 999;

    }
}

.business__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.business--box {
    margin-top: rem(45);

    @include mq(md) {
        margin-top: rem(40);
    }
}

.business--box--reverse {
    @include mq(md) {
        margin-left: rem(-10);
        position: relative;
        z-index: 1;
    }
}

.business__step {
    margin-top: rem(45);
    margin-inline: auto;

    @include mq(md) {
        margin-top: rem(117);
    }
}

.business__maintitle {
    margin-left: 30px;

    @include mq(md) {
        margin-left: rem(200);
    }
}

.business__tablebox {
    margin-top: rem(10);

    @include mq(md) {
        margin-top: rem(0);
    }
}

.business__tablebox--1 {
    margin-top: rem(60);

    @include mq(md) {
        margin-top: rem(118);
    }
}

.business__attention {
    font-size: rem(14);
    margin-top: rem(24);
    margin-inline: auto;

    @include mq(md) {
        color: #333;
        margin-top: rem(27);
        max-width: rem(908);
        width: 70%;
        margin-left: auto;
    }
}

.business__attention p {
    display: block;
}

.business__attention--bold {
    font-size: rem(18);
    font-weight: bold;

    @include mq(md) {
        font-size: rem(22);
        text-align: right;
        padding-right: rem(100);

    }
}