@use "global" as *;

footer {
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #F4F4F4E2;
    color: #333;


    @include mq(md) {
    }
}

.footer__inner {
    @include mq(md) {
        display: flex;
        justify-content: space-between;
        padding-left: rem(40);

    }
}

.footer__box {
    display: flex;
    flex-direction: column;
}

.footer__top {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;

    @include mq(md) {
        flex-direction: column;
        align-items:flex-start;
    }
}

.footer-nav__items {
    display: flex;
    flex-wrap: wrap;
    gap: rem(24) 0;
    justify-content: center;
    align-items: center;
    height: inherit;
    margin-top: rem(24);

    @include mq(md) {
        justify-content: flex-start;
        flex-wrap: nowrap;

    }
}

.footer-nav__item {
    height: inherit;
    font-size: rem(18);

}

.footer-nav__item a {
    color: #333333;
    font-size: rem(22);
    height: rem(54);
    display: grid;
    place-items: center;
    padding: 0 rem(15);
    letter-spacing: 0.125em;
    font-weight: 500;

}

.footer-nav__item a span {
    color: #333333;
    font-size: rem(12);
}



.footer__logo {
    display: inline-block;
    width: rem(132);
    margin-top: rem(30);

    @include mq(md) {
        margin-top: rem(42);
        margin-left: 10%;

    }
}

.footer__access {
    margin-top: rem(46);
    font-size: rem(14);
    margin-inline: auto;

    @include mq(md) {
        font-size: rem(16);
        margin-top: rem(22);
        margin-left: 10%;
    }
}

.footer__tellist {
    margin-top: rem(24);
    font-weight: bold;
    margin-inline: auto;

    @include mq(md) {
        display: flex;
        gap: rem(12);
        margin-top: rem(22);
        margin-left: 10%;
    }
}

.footer__tel {
    display: flex;
    align-items: center;
    gap: rem(3);
}

.footer__teltext {
    font-size: rem(16);
}

.footer__telnumber {
    font-size: rem(24);
}

.footer__tel img {
    width: rem(20);
}

.footer__sns {
    display: flex;
    margin-inline: auto;

    @include mq(md) {
        margin-left: 10%;
    }
}

.footer__sns a {
    display: block;
    width: rem(40);
    height: rem(40);
    margin-left: rem(6);
    margin-top: rem(22);
}

.footer__contact {
    display: none;

    @include mq(md) {
        display: block;
        width: 32%;
        background-color: #E3001BD0;
        text-align: center;
        padding: rem(165) rem(20);
    }
}

.footer__contact p {
    @include mq(md) {
        font-size: rem(20);
        color: $white;
    }
}

.footer__btn {
    @include mq(md) {
        margin-top: rem(34);
        width: 100%;
        text-align: center;
    }
}

.footer__contactbtn {
    display: block;

}
 
.footer__contactbtn a{
    @include mq(md) {
        background-color: #fff;
        color: #E8011D;
        padding: rem(20) rem(6);
        border-radius: rem(17);
    }
}


.footer__copyright {
    color: #333;
    font-size: rem(10);
    letter-spacing: 0.10909em;
    margin-top: rem(30);
    text-align: center;
    margin-bottom: rem(28);

    @include mq(md) {
        margin-top: rem(28);
        margin-bottom: rem(8);
        font-size: rem(12);
    }
}