@use "global" as *;

.box {
    color: #333333;
    background-color: #fff;
    box-shadow: 5px 5px 6px #00000029;
    padding-top: rem(28);
    padding-bottom: rem(20);

    @include mq(md) {
        margin-top: rem(186);
        padding: rem(25) rem(50) rem(50);
        max-width: rem(562);
    }
}

.box__contents {
    padding: 0 rem(20);

    @include mq(md) {
        padding: 0;
        max-width: rem(550);
    }
}

.box__maintitle {
    font-size: rem(22);
    letter-spacing: 0.1em;
    font-weight: 600;
    position: relative;

    @include mq(md) {
        font-size: rem(22);
        line-height: 1.36;
        letter-spacing: 0.02em;
    }
}

.box__maintitle::before {
    position: absolute;
    content: "";
    border: rem(2) solid #F00C27;
    height: rem(60);
    left: -5%;

    @include mq(md) {
        left: -3%;
        top: -4%;
    }

}

.box__text {
    font-size: rem(14);
    letter-spacing: 0.02em;
    line-height: 1.45;
    letter-spacing: 0.125em;
    margin-top: rem(28);
    text-align: start;

    @include mq(md) {
        letter-spacing: 0.03125em;
        line-height: 1.5;
    }
}

.box__btn {
    @include mq(md) {
        margin-top: rem(60);
        text-align: center;
    }
}