@use "global" as *;

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: rem(90);
  margin-left: 50%;
  transform: translateX(-50%);

  @include mq(md) {
    font-size: rem(62);
    max-width: rem(908);
    width: 70%;
  }
}

th, td {
  border-bottom: 1px solid #333333;
  text-align: left;
  font-size: rem(18);
  letter-spacing: 0.1em;
  padding: rem(20) 0;
}

th {
  color: #FDFDFD;
  background-color: #FA384F;
  text-align: center;
  width: 30%;
  padding: rem(8);

  
  @include mq(md) {
    padding: rem(16) 0;
    width: 40%;
  }
}

th:first-child {
  border-right: 0.5px solid #333333;
  padding: rem(8);
  
  @include mq(md) {
    padding: rem(16) rem(6);
  }

}

td {
  color: #333;
  border: 1px solid #333333;
  width: 60%;
  text-align: center;


  @include mq(md) {
    padding: rem(16) 0;
  }
}