@use "global" as *;

.index-case {
    margin-top: rem(52);
    position: relative;
    z-index: 2;

    @include mq(md) {
        margin-top: rem(240);
    }
}

.index-case__bg1 {
    top: 12%;
    @include mq(md) {
        top: 0;
    }
}

.index-case__bg2 {
    top: 44%;
    @include mq(md) {
        top: 34%;
    }
}

.index-case__bg3 {
    top: 72%;
    @include mq(md) {
        top: 59%;
    }
}


.index-case__inner {
    margin: auto;
}

.index-case__cube1 {
    margin-top: rem(-20);


    @include mq(md) {
        margin-top: rem(6);
    }
}

.index-case__cube2 {
    margin-top: rem(-20);
    margin-left: auto;

    @include mq(md) {
        margin-top: rem(-20);
    }
}

.index-case__cube3 {
    margin-top: rem(-20);

    @include mq(md) {
        margin-top: rem(-20);
    }
}


.index-case__topwrap {
    display: flex;
    flex-direction: column-reverse;
    margin-top: rem(76);

    @include mq(md) {
        flex-direction: row;
        margin-top: 0;
    }
}

.index-case__topimg {
    aspect-ratio: 522 / 392;
    max-width: rem(280);
    overflow: hidden;
    margin: auto;
    @include mq(md) {
        aspect-ratio: 430 / 322;
        max-width: rem(522);
    }
}

.index-case__topimg img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}


.index-case__box1 {
    background-color: #fff;
    box-shadow: 5px 5px 6px #00000029;
    margin-top: rem(84);
    padding: rem(24) rem(12) rem(22) rem(10);

    @include mq(md) {
        margin-top: rem(150);
        max-width: rem(560);
        padding: rem(25) rem(50) rem(50);

    }
}

.index-case__contents {
    color: #333333;
}

.index-case__maintitle {
    font-size: rem(22);
    letter-spacing: 0.1em;
    font-weight: bold;
    position: relative;
    padding-left: rem(20);


    @include mq(md) {
        font-size: rem(22);
        line-height: 1.36;
        letter-spacing: 0.02em;
    }
}

.index-case__maintitle::before {
    position: absolute;
    content: "";
    border: rem(2) solid #F00C27;
    height: rem(58);
    left: -0.5%;
    top: -20%;
}

.index-case__wrap {
    display: flex;
    justify-content: center;
    margin-top: rem(50);

    @include mq(md) {
        margin-top: rem(46);
        align-items: end;

    }
}

.index-case__lefts {
    position: relative;
}

.index-case__lefts::before {
    position: absolute;
    content: "";
    border: rem(1) solid #F00C27;
    height: rem(216);
    left: 110%;

    @include mq(md) {
        height: rem(270);
        left: 110%;
        top: -4%;
    }
}

.index-case__left:nth-child(2) {
    @include mq(md) {
        margin-top: rem(46);
    }
}

.index-case__subtitle {
    font-size: rem(22);
    font-weight: bold;
    text-align: center;


    @include mq(md) {
        font-size: rem(28);
        line-height: 1.07;
    }
}

.index-case__subtitle--2 {
    margin-top: rem(56);
}

.index-case__text {
    font-size: rem(22);
    margin-top: rem(12);
    font-weight: bold;
    text-align: center;

    @include mq(md) {
        font-size: rem(42);
    }
}

.index-case__text--margin {
    @include mq(md) {
        margin-top: rem(40);
    }
}

.index-case__text span {
    font-size: rem(24);
    font-weight: normal;


    @include mq(md) {
    }
} 

.index-case__right {
    margin-left: rem(40);

    @include mq(md) {
        margin-left: rem(70);
    }
}

.index-case__right--1 {
    margin-top: rem(40);

    @include mq(md) {
        margin-top: 0;
        margin-bottom: rem(80);
    }
}

.index-case__attention {
    margin-top: rem(40);
    text-align: right;

    @include mq(md) {
        text-align: center;
        font-size: rem(16);
        margin-top: rem(40);
        letter-spacing: 0.1em;
    }
}

.index-case__second {
    margin-top: rem(140);

    @include mq(md) {
        display: flex;
        margin-top: rem(40);
    }
}


.index-case__img1 {
    aspect-ratio: 474 / 355;
    overflow: hidden;
    max-width: rem(352);
    margin: auto;

    @include mq(md) {
        aspect-ratio: 474 / 355;
        overflow: hidden;
        max-width: rem(474);
    }
}

.index-case__img1 img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.index-case__box2 {
    background-color: #fff;
    box-shadow: 5px 5px 6px #00000029;
    margin-top: rem(42);
    padding: rem(24) rem(12) rem(22) rem(10);

    @include mq(md) {
        margin-top: rem(150);
        max-width: rem(560);
        padding: rem(25) rem(30) rem(50);
        margin-left: rem(-100);

    }
}

.index-case__lefts--02::before {
    position: absolute;
    content: "";
    border: rem(1) solid #F00C27;
    height: rem(134);

    @include mq(md) {
        height: rem(106);
        left: 110%;
        top: 4%;
    }
}

.index-case__third {
    @include mq(md) {
        margin-top: rem(40);
        display: flex;
        flex-direction: row-reverse;
    }
}

.index-case__img2 {
    aspect-ratio: 474 / 355;
    overflow: hidden;
    max-width: rem(352);
    margin: auto;
    margin-top: rem(140);

    @include mq(md) {
        max-width: rem(474);
        margin-left: rem(-20);
    }
}

.index-case__img2 img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.index-case__box3 {
    background-color: #fff;
    box-shadow: 5px 5px 6px #00000029;
    margin-top: rem(30);
    padding: rem(24) rem(12) rem(22) rem(10);

    @include mq(md) {
        margin-top: rem(150);
        max-width: rem(560);
        padding: rem(25) rem(30) rem(50);
    }
}

.index-case__wrap2 {
    justify-content: center;
    margin-top: rem(60);

    @include mq(md) {
        margin-top: rem(80);
        gap: rem(60);
    }
}

.index-case__lefts--03 {
    width: 100%;

    @include mq(md) {
        width: auto;
    }
}

.index-case__lefts--03::before {
    position: absolute;
    content: "";
    border: rem(1) solid #F00C27;
    height: rem(134);
    left: 100%;


    @include mq(md) {
        height: rem(106);
        left: 130%;
        top: 4%;
    }
} 

.index-case__left--padding {

    @include mq(md) {
        padding-left: rem(28);
    }
}

.index-case__right--center {
    width: 100%;
    margin-left: 0;

    @include mq(md) {
        width: auto;
        text-align: center;
        // padding-left: rem(80);

    }
}

.index-case__right--right {
    width: 100%;
    margin-left: 0;

    @include mq(md) {
        width: auto;
        padding-left: rem(60);
    }
}

.index-case__maintitle--height::before  {
    top: 15%;

    @include mq(md) {
        top: 8%;
     }
}


.index-case__btn {
    margin-top: rem(66);
    margin-bottom: rem(60);
    text-align: center;
    
    @include mq(md) {
        margin-top: 0;
        margin-bottom: rem(130);
    }
}